












import Vue from 'vue'
import { Component, Watch } from 'vue-property-decorator'
import { postMessage, addMessageListener } from '@/utils/messaging'
import * as MESSAGES from '@/constants/messages'
import Parse from '@/Parse.vue'
import Offline from '@/Offline.vue'

@Component({
  components: {
    Parse,
    Offline,
  },
})
export default class App extends Vue {
  curTab: 'parse' | 'offline' = 'parse'

  @Watch('curTab')
  onTabChange (val: string) {
    console.log('cur tab changed ', val)
    if (val === 'parse') {
      (this.$refs['parse-page'] as Parse).clearData()
    } else if (val === 'offline') {
      (this.$refs['parse-page'] as Offline).clearData()
    }
    postMessage(MESSAGES.SWITCH_PAGE_TAB, val)
  }

  mounted () {
    addMessageListener(MESSAGES.SWITCH_PAGE_TAB, (tab: string) => {
      if (tab !== this.curTab) {
        if (tab === 'parse') {
          this.curTab = 'parse'
        } else if (tab === 'offline') {
          this.curTab = 'offline'
        }
      }
    })
  }

  createOffline (magnetUri: string) {
    this.curTab = 'offline';
    (this.$refs['offline-page'] as Offline).createOfflineTask(magnetUri)
  }
}
