import Vue from 'vue'
import VueI18n from 'vue-i18n'
import App from '@/App_v2.vue'
import i18nMessages from '@/i18n'
import { postMessage } from '@/utils/messaging'
import * as MESSAGES from '@/constants/messages'
import * as GA_EVENTS from '@/constants/gaEvents'
import * as ZHUGE_EVENTS from '@/constants/zhugeIOEvents'
import '@/init'

const appInst = new Vue({
  i18n: new VueI18n({
    locale: 'zh',
    messages: i18nMessages,
  }),
  render: h => h(App),
}).$mount('#app')

setTimeout(() => {
  if (!window.enfiToken) {
    appInst.$gaVisitor
      .event(
        GA_EVENTS.EVENT_NOT_INIT_FROM_CLIENT,
        GA_EVENTS.EVENT_TYPE_EVT
      )
    window.zhuge.track(ZHUGE_EVENTS.EVENT_NOT_INIT_FROM_CLIENT)
    postMessage(MESSAGES.GET_INIT_DATA)
  }
}, 3000)
